<template>
  <div v-title data-title="编辑博客">
    <el-container style="margin-right:250px;margin-left:250px; margin-top:30px;">
      <el-container style="" direction="vertical">
        <el-form ref="infoForm" :model="Blog" :rules="rules" label-width="120px" enctype="multipart/form-data">
          <el-form-item label="博客标题" prop="title">
            <el-input
                type="textarea"
                placeholder="请输入博客标题"
                v-model="Blog.title"
                maxlength="30"
                show-word-limit
                style="width: 70%; float: left"
              >
            </el-input>
          </el-form-item>

          <el-form-item label="内容简介" prop="simple">
            <el-input
                type="textarea"
                placeholder="默认为博客正文的前30字"
                v-model="Blog.simple"
                maxlength="30"
                show-word-limit
                style="width: 70%; float: left"
              >
            </el-input>
          </el-form-item>

          <el-form-item label="博客标签" prop="tags">
            <div class="grid-content" style="text-align:left; overflow:visible;">
              请选择标签：<br>
              <!--需要输入框长一点-->
              <el-select
                v-model="Blog.tags"
                filterable
                default-first-option
                size="medium"
                placeholder="请选择文章标签">
                <el-option
                  v-for="item in Blog.options"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </div>
          </el-form-item>

          <el-form-item label="博客正文" prop="content">
            <div class="edit_container">
              <quill-editor v-model="Blog.content"
                            ref="myQuillEditor"
                            class="editer"
                            :options="editorOption" @ready="onEditorReady($event)">
              </quill-editor>
            </div>
          </el-form-item>

          <el-form-item>
            <el-upload
              class="upload-demo"
              drag
              ref="upload"
              action="url"
              :auto-upload="false"
              :before-upload="beforeUpload"
              :file-list="fileList"
              multiple>
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            </el-upload>
          </el-form-item>

          <el-form-item>
            <el-button type="primary" @click="onSubmit">发布文章</el-button>
            <el-button type="danger" >取消</el-button>
          </el-form-item>
        </el-form>
      </el-container>
    </el-container>
  </div>
</template>

<script>
// import { addQuillTitle } from '../quill-title.js'
import axiosInstance from '../../api/index'
import {getCategory, submitBlog} from '../../api/api'
const toolbarOptions = [
  ['bold', 'italic', 'underline', 'strike'],
  ['blockquote', 'code-block'],
  [{ 'header': 1 }, { 'header': 2 }],
  [{ 'list': 'ordered' }, { 'list': 'bullet' }],
  [{ 'script': 'sub' }, { 'script': 'super' }],
  [{ 'indent': '-1' }, { 'indent': '+1' }],
  [{ 'direction': 'rtl' }],
  [{ 'size': ['small', false, 'large', 'huge'] }],
  [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
  [{ 'color': [] }, { 'background': [] }],
  [{ 'font': [] }],
  [{ 'align': [] }],
  ['clean'],
  ['link', 'image', 'video']
]
export default {
  name: 'test',
  data () {
    return {
      url: 'https://jsonplaceholder.typicode.com/posts/',
      activeName: 'first',
      UserId: 'User',
      urls: {
        Home: '/',
        EditBlog: '/EditBlog'
      },
      BlogID: '',
      Blog: {
        title: '',
        simple: '',
        content: '',
        options: [{
          value: 'HTML',
          label: 'HTML'
        }, {
          value: 'java',
          label: 'java'
        }],
        tags: '', // 为被选中标签的value属性值
        files: []
      },
      editorOption: {
        theme: 'snow',
        boundary: document.body,
        modules: {
          toolbar: {
            container: toolbarOptions
          }
        },
        placeholder: 'Insert text here ...',
        readOnly: false
      },
      quillUpdateImg: false, // 根据图片上传状态来确定是否显示loading动画，刚开始是false,不显示
      header: {token: sessionStorage.token},
      // 表单验证
      rules: {
        title: [
          {required: true, message: '请输入标题', trigger: 'blur'}
        ],
        tags: [
          {required: true, message: '标签不能为空', trigger: 'blur'}
        ],
        content: [
          {required: true, message: '必须填写正文或资源的简介', trigger: 'blur'}
        ]
      },
      dialogImageUrl: '',
      dialogVisible: false,
      disabled: false,
      fileList: []
      // param:''
    }
  },
  computed: {
    editor () {
      return this.$refs.myQuillEditor.quill
    }
  },
  mounted () {
    // 初始化
    // addQuillTitle()
  },
  methods: {
    onEditorReady (editor) {
    },
    onSubmit () {
      this.$refs.infoForm.validate((valid) => {
        if (valid && this.BlogID === '') { // 创建新博客
          // 4.1，下面append的东西就会到form表单数据的this.param中；
          // this.param.append('title', this.Blog.title)
          // this.param.append('simple',this.Blog.simple)
          // this.param.append('tags',this.Blog.tags)
          // this.params.append('content',this.Blog.content)
          // this.$refs.upload.submit()
          // let config = {
          // headers: {
          //  'Content-Type': 'multipart/form-data'
          // }
          // }
          submitBlog(this.Blog.title, this.Blog.content, new Date().getDate(), this.Blog.tags).then(response => {
          // image file
            if (response.status === 201) {
              console.log(response)
              alert('上传成功！')
              this.$router.push('/MainPage')
            } else {
              console.log(response)
              alert('上传失败！')
            }
          }).then(error => {
            console.log(error)
          })
        } else if (valid && this.BlogID !== '') { // 更新博客
          const axios = axiosInstance
          axios.put('http://127.0.0.1:8000/edit/' + this.BlogID + '/', {
            params: {
              title: this.Blog.title,
              body: this.Blog.content,
              pub_date: new Date().getDate(),
              category: {
                id: this.Blog.tags
              }
              // image file
            }
          }).then(response => {
          // image file
            if (response.status === 200) {
              console.log(response)
              alert('更新成功！')
              this.$router.push('/MainPage')
              this.BlogID = ''
            } else {
              console.log(response)
              alert('更新失败！')
            }
          }).then(error => {
            console.log(error)
          })
        }
      })
    },
    beforeUpload (file) {
      this.Blog.files.push(file.name, file.raw)// ???????
      return false
    },
    getCategory () {
      getCategory().then(response => {
        if (response.status === 200) {
          let i = 0
          let sum = response.data.length
          for (i = 0; i < sum; i++) {
            this.Blog.options[i].label = response.data[i].name
            this.Blog.options[i].value = response.data[i].id
          }
        }
      })
    },
    getBlog (ID) {
      const axios = axiosInstance
      axios.get('http://127.0.0.1:8000/list/' + ID).then(response => {
        if (response.status === 200) {
          console.log(response)
          this.Blog.title = response.data.title
          this.Blog.simple = response.data.excerpt
          this.Blog.content = response.data.body
          this.Blog.tags = response.data.category.id
          // 文件 图片
        } else {
          this.$message({
            message: '获取详情失败' + response.message,
            type: 'error'
          })
        }
      })
    }
  },
  components: {
  },
  created () {
    this.getCategory()
    this.BlogID = this.$route.query.BlogId
    if (this.$route.query.BlogId !== '') {
      this.getBlog(this.$route.query.BlogId)
    }
  }
}

</script>

<style scoped>
  .el-message-box {
    z-index: 2000;
  }
</style>



